.companion-card {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  padding: 15px 15px 73px 14px;
  box-sizing: border-box;
  border-radius: 20px;
  background-color: $white;
}

.companion-card__info {
  display: flex;
  width: 100%;
  padding: 0 0 0 4px;
}

.companion-card__user {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.companion-card__picture {
  margin-right: 16px;
}

.companion-card__like {
  position: absolute;
  top: 53px;
  left: 68px;
  width: 31px;
  height: 31px;
  border: none;
  border-radius: 50%;
  background: $bg-grey-1 url("../img/heart.svg") no-repeat center center;
  background-size: 13px 11px;
}

.companion-card__like--active {
  background: $bg-pink-like url("../img/heart-active.svg") no-repeat center center;
  background-size: 13px 11px;
}

.companion-card__name {
  position: relative;
  width: 78px;
  margin-bottom: 6px;
  font-size: 20px;
  line-height: 20px;
  text-decoration: none;
  color: $dark-blue-2;

  @include primary-font($bold);
}

.companion-card__name-text {
  display: inline-block;
  padding: 0;
  margin: 0;

  &::before {
    top: 7px;
    right: -66px;
  }
}

.companion-card__points {
  margin-bottom: 17px;
  font-size: 14px;
  line-height: 21px;
  color: $dark-blue-3;

  @include primary-font($bold);
}

.companion-card__tags {
  order: 3;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  width: 180px;
  margin-left: -79px;
  margin-bottom: 12px;
}

.companion-card__directions {
  position: relative;
  width: 100%;
  min-height: 98px;
  padding: 0 0 2px 0;
  box-sizing: border-box;

  &::before {
    z-index: 1;
    position: absolute;
    top: 10px;
    left: -16px;
    display: block;
    content: "";
    width: calc(100% + 29px);
    height: 1px;
    background-color: $border-grey-2;
  }

  &::after {
    z-index: 1;
    position: absolute;
    bottom: -11px;
    left: -16px;
    display: block;
    content: "";
    width: calc(100% + 29px);
    height: 1px;
    background-color: $border-grey-2;
  }
}

.companion-card__directions--fix {
  height: 122px; // Почему то только этом элементе высота не соответствует нормальной
}

.companion-card__title {
  z-index: 2;
  position: relative;
  display: inline-block;
  padding: 0 11px;
  margin: 0 0 14px 0;
  font-size: 15px;
  text-transform: lowercase;
  color: $text-grey;
  box-sizing: border-box;

  @include primary-font($normal);
  background-color: $white;
}

.companion-card__list {
  padding: 0;
  margin: 0 0 0 8px;
  list-style: none;
}

.companion-card__item {
  margin: 0 0 10px 0;
}

.companion-card__transport {
  width: 100%;
}

.companion-card__wrapper {
  display: flex;
  width: 100%;
}

.companion-card__transport-list {
  display: flex;
}

.companion-card__level {
  margin: 0 auto;

  .companion-card__title {
    margin-bottom: 10px;
  }
}

.companion-card__btn {
  position: absolute;
  left: 20px;
  bottom: 23px;
  width: 230px;
}

.companion-card__user-actions {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

@media (min-width: $tablet-width) {
  .companion-card {
    flex-direction: row;
    align-items: flex-start;
    padding: 34px 20px 25px 31px;
  }

  .companion-card__info {
    width: 470px;
    margin-bottom: 21px;
    margin-right: 93px;
  }

  .companion-card__like {
    order: 2;
    position: initial;
    width: 50px;
    height: 50px;
    background-size: 20px;
  }

  .companion-card__user {
    position: relative;
    width: 255px;
    margin-left: 15px;
  }

  .companion-card__name {
    position: initial;
    width: 100%;
    margin-bottom: 17px;
    font-size: 30px;
    line-height: 30px;
  }

  .companion-card__name-text {
    position: relative;

    &::before {
      top: 10px;
      right: -32px;
    }
  }

  .companion-card__points {
    order: 3;
    display: inline-block;
    align-self: flex-end;
    width: 49px;
    margin: 0 0 13px 6px;
    font-size: 20px;
    line-height: 20px;
  }

  .companion-card__list {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
  }

  .companion-card__item {
    width: auto;
    margin: 0 28px 0 0;
  }

  .companion-card__title {
    display: inline-block;
    margin: 0 0 18px 41px;
  }

  .companion-card__user-actions {
    order: 3;
  }

  .companion-card__tags {
    width: 100%;
    margin: 0 0 45px 5px;
  }

  .companion-card__transport {
    margin-bottom: 10px;
  }

  .companion-card__transport-list {
    flex-direction: column;
    align-items: center;
  }

  .companion-card__btn {
    position: initial;
    width: 148px;
    padding: 25px 0;
  }

  .companion-card__directions {
    order: 3;
    min-height: 50px;

    &::before {
      top: 10px;
      left: -31px;
      width: calc(100% + 52px);
    }

    &::after {
      display: none;
    }
  }

  .companion-card__directions--fix {
    height: 100%;
  }

  .companion-card__wrapper {
    justify-content: center;
    flex-wrap: wrap;
    width: 60px;
    height: 190px;

    .companion-card__title {
      display: none;
    }
  }
}

@media (min-width: $desktop-width) {
  .companion-card {
    position: relative;
    width: 950px;
    padding: 0;
    margin: 0 0 30px 0;
    flex-direction: row;
    align-items: flex-start;

    &::after {
      position: absolute;
      top: 60px;
      right: 319px;
      display: block;
      content: "";
      width: 1px;
      height: 187px;
      background: linear-gradient(to bottom, rgba($bg-grey-1, 0) 0%, $bg-grey-1 25%, $bg-grey-1 65%, rgba($bg-grey-1, 0) 100%);
    }
  }

  .companion-card__picture {
    display: flex;
  }

  .companion-card__info {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 600px;
    padding: 0;
    margin: 0 65px 0 0;
  }

  .companion-card__user {
    width: 257px;
    padding-top: 45px;
  }

  .companion-card__user-actions {
    margin-bottom: 20px;
  }

  .companion-card__tags {
    margin: 0 0 47px 5px;
  }

  .companion-card__points {
    margin: 0;
    padding: 0 0 15px 6px;
  }

  .companion-card__directions {
    display: flex;
    width: 200px;
    padding-top: 46px;
    padding-bottom: 90px;
    margin: 0;

    &::before {
      display: none;
    }

    .companion-card__list {
      flex-direction: column;
      justify-content: flex-start;
    }
  }

  .companion-card__item {
    width: 100%;
    margin: 0 0 13px 0;

    &:last-child {
      margin: 0;
    }
  }

  .companion-card__title {
    display: none;
  }

  .companion-card__wrapper {
    order: 3;
    position: absolute;
    bottom: 39px;
    right: 8px;
    width: 282px;
    height: 60px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .companion-card__transport {
    margin: 0;
    padding: 0;
    width: 150px;
    height: 40px;
  }

  .companion-card__list {
    flex-direction: row;
    align-items: center;
  }

  .companion-card__name-text {
    &::before {
      top: 12px;
      right: 0;
      left: -25px;
    }
  }
}
